import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Subscription } from 'rxjs';
import { MetricsService } from 'src/app/services/metrics.service';

@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.scss']
})
export class PostsComponent implements OnInit {

  @ViewChild('userForm', { static: true }) userForm: NgForm;

  metrics = ['Angular', 'React', 'Vue'];
  fromMetrics = ['Angular', 'React', 'Vue'];
  toMetrics = ['Angular', 'React', 'Vue'];

  metric: string;
  fromMetric: string;
  toMetric: string;
  value: string;
  result: string;

  message: any = {};
  subscription: Subscription;

  constructor(private metricService: MetricsService) { 

  }

  ngOnInit() {

    this.parseMetricCategories();
  }

  parseMetric_() {

    this.metricService.convertMetric(
      this.userForm.form.controls.metric.value,
      this.userForm.form.controls.fromMetric.value,
      this.userForm.form.controls.toMetric.value,
      this.userForm.form.controls.value.value)
    .subscribe(data => {
        console.log(data);
        if (data.length != 0) {
          this.result = data;
        }
      }, error => {
        this.userForm.form.controls.value.setValue("0");
        this.userForm.form.controls.result.setValue("0"); 
      }
    );

  }

  parseMetric() {

    this.metricService.convertMetric(this.metric,this.fromMetric,this.toMetric,this.value)
    .subscribe(data => {
        console.log(data);
        if (data.length != 0) {
          this.result = data;
        }
      }, error => console.log(error)
    );

  }

  parseMetricByMetricCatogoryExclFromMetric_(): void {

    this.metricService.getMetricByMetricCatogoryExclFromMetric(
      this.userForm.form.controls.metric.value,
      this.userForm.form.controls.fromMetric.value
      )
    .subscribe(data => {
        console.log(data);
        if (data.length != 0) {
          this.toMetrics = data;
          this.toMetric = this.toMetrics[0];
          this.value = "0";
          this.result = "0";
          this.parseMetric_()
        }
      }, error => console.log(error)
    );
  }

  parseMetricByMetricCatogoryExclFromMetric(): void {

      this.metricService.getMetricByMetricCatogoryExclFromMetric(this.metric,
        this.fromMetric
        )
      .subscribe(data => {
          console.log(data);
          if (data.length != 0) {
            this.toMetrics = data;
            this.toMetric = this.toMetrics[0];
            this.value = "0";
            this.result = "0";
            this.parseMetric()
          }
        }, error => console.log(error)
      );
  }

  parseMetricByMetricCatogory_(): void {


      this.metricService.getMetricByMetricCatogory(
        this.userForm.form.controls.metric.value)
      .subscribe(data => {
          console.log(data);
          if (data.length != 0) {
            this.fromMetrics = data;
            this.fromMetric = this.fromMetrics[0];
            this.parseMetricByMetricCatogoryExclFromMetric_()
          }
        }, error => console.log(error)
      );

  }

  parseMetricByMetricCatogory(): void {


      this.metricService.getMetricByMetricCatogory(this.metric)
      .subscribe(data => {
          console.log(data);
          if (data.length != 0) {
            this.fromMetrics = data;
            this.fromMetric = this.fromMetrics[0];
            this.parseMetricByMetricCatogoryExclFromMetric()
          }
        }, error => console.log(error)
      );

  }

  parseMetricCategories() {

    this.metricService.getMetricCatogories()
    .subscribe(data => {
        console.log(data);
        this.metrics = data;
        this.metric = this.metrics[0];
        this.parseMetricByMetricCatogory();
      }, error => console.log(error)
    );
  }

  onClickSubmit(result) {
     console.log("You have entered : " + result.surname); 
  }

  onChange(deviceValue) {
    this.parseMetricByMetricCatogory_();
    console.log(deviceValue);
  } 
}
