import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class MetricsService {

  private baseUrl = 'convert';
  
  constructor(private http: HttpClient) { }

  public getMetricCatogories(): Observable<any> {
    return this.http.get(`${this.baseUrl}/list`);
  }

  public getMetricByMetricCatogory(metricCategory: string): Observable<any> {
    return this.http.get(`${this.baseUrl}/${metricCategory}`);
  }

  public getMetricByMetricCatogoryExclFromMetric(metricCategory: string, fromMetric: string): Observable<any> {
    return this.http.get(`${this.baseUrl}/${metricCategory}/${fromMetric}`);
  }

  public convertMetric(metricCategory: string, fromMetric: string, toMetric: string, value: string): Observable<any> {
    return this.http.get(`${this.baseUrl}/${metricCategory}/${fromMetric}/${toMetric}/${value}`);
  }
}
